import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  FormArray,
  Validators,
  FormControl,
} from '@angular/forms';

import { AuthService } from '../core/auth.service';
import { LanguageService } from '../core/language.service';
import { UtilityService } from '../core/utility.service';
import { list } from './../signup/countryCode';
import { MustMatch } from 'src/app/shared/directives/must-match.validator';
import { MenuController } from '@ionic/angular';

@Component({
  selector: 'app-service-provider-signup2',
  templateUrl: './service-provider-signup2.page.html',
  styleUrls: ['./service-provider-signup2.page.scss'],
})
export class ServiceProviderSignup2Page implements OnInit {
  registerForm: FormGroup;
  selectedCar: number;
  model: any = {};
  countryList = list;
  services;
  languages;
  constructor(
    private authService: AuthService,
    private utility: UtilityService,
    private languageService: LanguageService,
    private fb: FormBuilder,
    private menuCtrl: MenuController
  ) {}

  ngOnInit() {
    console.log('ngOnit');
    this.getServiceCategory();
    this.menuCtrl.enable(false);

    this.buildForm();
    this.languages = this.languageService.languages;
  }
  ionViewWillEnter() {
    this.menuCtrl.swipeGesture(false);
  }

  buildForm() {
    this.registerForm = this.fb.group({
      postalCode: [''],
      companyName: [''],
      // services_given: ['', [Validators.required]],
      gender: [''],
      fiscalNumber: [''],
      consultation: ['direct'],
    });
  }
  get petType() {
    return this.registerForm.get('petType') as FormArray;
  }
  // pattern=""

  get f() {
    return this.registerForm.controls;
  }

  addPetType() {
    const pet = this.fb.group({
      petType: ['', [Validators.required]],
    });
    this.petType.push(new FormControl('', Validators.required));
  }

  removePetType(i) {
    this.petType.removeAt(i);
  }

  handleChange(e) {
    console.log(e);
    const val = e.detail.value;
    this.petType.clear();
    for (let i = 1; i <= val; i++) {
      this.addPetType();
    }
  }
  submit(form) {
    Object.keys(form.controls).forEach((key) => {
      form.get(key).markAsTouched();
    });
    if (form.status.toLowerCase() === 'valid') {
      const formObject = form.value;
      formObject.role = 'service provider';

      delete formObject.cPassword;
      // formObject.petType = formObject.petType.map((v) => v.petType);

      console.log(formObject);
      let signUpValues = JSON.parse(this.utility.getLocal('signUpData'));
      console.log(signUpValues);

      let dict = {
        firstname: signUpValues?.firstname,
        lastname: signUpValues?.lastname,
        gender: formObject?.gender,
        companyName: formObject?.companyName,
        fiscalNumber: formObject?.fiscalNumber,
        address: signUpValues?.address,
        country: signUpValues?.country,
        city: signUpValues?.city,
        zipCode: signUpValues?.zipCode,
        email: signUpValues?.email,
        password: signUpValues?.password,
        numberOfPet: signUpValues?.numberOfPet,
        petType: '',
        // services: formObject?.services_given,
        services: signUpValues?.services_given,
        consultationType: formObject?.consultation,
        role: 'service provider',
      };

      this.utility.present();
      this.authService.register(dict).subscribe(
        (res: any) => {
          console.log(res);
          this.utility.dismiss();
          this.utility.setLocal(
            'cred',
            window.btoa(
              JSON.stringify(formObject.email + ':' + formObject.password)
            )
          );
          if (res.status == true) {
            this.utility.setLocal('userObj', JSON.stringify(res.data));

            let json = {
              email: signUpValues?.email,
              password: signUpValues?.password,
              role: 'service provider',
            };

            this.authService.login(json).subscribe((resp) => {
              console.log(resp);
              const token = resp['token'];
              const userObj = resp['data'];
              this.utility.setLocal('token', token);
              this.utility.setLocal('userObj', JSON.stringify(userObj));
              this.utility.dismiss();

              this.utility.navigateRoot('/tabs/calender-appointment');
              this.utility.eventBus.sideMenu.next('service-provider');
            });
            this.utility.navigate('/login');
          } else {
            // this.utility.showAlert(res['message']);
            this.utility.presentToastWithOptions(res['message']);
          }
        },
        (err) => {
          this.utility.dismiss();
          // this.utility.hideLoading();
        }
      );
    }
  }

  switchLanguage(e) {
    console.log(e);
    const language = e.toLowerCase();
    this.languageService.useLanguage(language.code);
  }

  ngOnDestroy() {
    console.log('working');
    this.menuCtrl.enable(true);
  }
  ionViewWillLeave() {
    console.log('leaving');
    this.menuCtrl.enable(true);
    this.menuCtrl.swipeGesture(true);
  }
  getServiceCategory() {
    this.authService.getServiceCategory().subscribe((res) => {
      this.services = res['data'];
      console.log(this.services);
    });
  }
}
